.btn {
	cursor: pointer;
	display: inline-block;
	transition: all 0.2s;
	&:link,
	&:visited {
		padding: 2rem 4rem;
		font-size: 1.6rem;
		font-weight: 400;
		border: none;
		outline: none;

		backface-visibility: hidden;
		&:not(:last-child) {
			margin-right: 4rem;
		}

		@media only screen and (max-width: 680px) {
			padding: 4rem 6rem;
			font-size: 2rem;
		}
	}

	&--black {
		background-color: $navy;
		color: $white;
	}

	&--text {
		font-weight: 700;
		font-size: 1.6rem;
		@media only screen and (max-width: 680px) {
			font-weight: bold;
			font-size: 2rem;
		}
	}

	&:hover,
	&:focus,
	&:active {
		transform: scale(0.98) translateY(-1rem);
		_color: $white;
	}
}
