.popup {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 30vw;
	height: 30vh;
	margin: 0 auto;
	display: none;
	&__content {
		display: flex;
		_height: 10rem;
		align-items: center;
		padding: 3.2rem;
		border-radius: 3px;
		border-left: 5px solid #ffda6b;
		background-color: $white;
		box-shadow: 0 2rem 4rem rgba($black, 0.2);
		position: relative;
	}
	&__right {
		margin-left: 3rem;
	}

	&__left {
	}

	&__image {
		width: 10rem;
		max-width: 100%;
	}

	&__title {
		font-size: 2.4rem;
	}

	&__text {
		font-size: 1.8rem;
	}

	&__close {
		cursor: pointer;
		width: 4rem;
		height: 4rem;
		font-size: 3.2rem;
		position: absolute;
		top: 2rem;
		right: 2.5rem;

		&:hover,
		&:active {
			color: $red;
		}
	}

	@media only screen and (max-width: 680px) {
		width: 90%;
	}
}

.show {
	display: block;
}
