*,
*:before,
*:after {
	box-sizing: inherit;
}

html {
	font-size: 62.25%;
	scroll-behavior: smooth;
}

body {
	//url('../img/ep_naturalblack.png')
	background: #fff;
	font-size: 1.4rem;
	line-height: 1.6;
	font-size: 14px;
	color: $color-primary;
	-webkit-font-smoothing: antialiased;
	-webkit-text-size-adjust: 100%;
	box-sizing: border-box;
	overflow-x: hidden;
}

section {
	display: -webkit-box;
	backface-visibility: hidden;
}

.fadeIn {
	animation: moveInBottom 1s ease;
}

@media only screen and (max-width: 680px) {
	html {
		font-size: 32.25%;
	}
}
