.header {
	width: 100vw;
	height: 90vh;
	background-color: $color-background;
	animation: moveInBottom 3s ease-in;
}

.clip-path {
	clip-path: polygon(0% 0%, 100% 0%, 100% 70vh, 0% 100%);
}

@media only screen and (max-width: 680px) {
	.header {
		height: 70vh;
		clip-path: polygon(0% 0%, 100% 0%, 100% 60vh, 0% 100%);
	}
}
