.project {
	font-size: 1.8rem;
	&__title {
		font-size: inherit;
		text-transform: uppercase;
		font-weight: 500;
		letter-spacing: 0.3rem;
	}

	&__description {
		font-size: 1.8rem;

		&--body {
			font-size: inherit;
		}

		&--sub {
			color: rgba($color: $color-primary, $alpha: 0.8);
		}
	}

	&__link {
		font-size: 1.8rem;
		color: $color-primary;
		padding: 0.8rem 0;
		border-bottom: 1px solid $color-primary;
	}

	&__image {
		position: relative;
		text-align: center;
		cursor: pointer;
		height: 100%;
		transform: scale(1.1);
		transition: all 0.3s ease;
		img {
			width: 100%;
			height: 100%;
			max-width: 100%;
			background-size: cover;
		}

		&:hover {
			transform: scale(1.2);
		}

		&:hover .overlay {
			opacity: 1;
			visibility: visible;
		}
	}
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba($color: #000000, $alpha: 0.5);
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease;

	&__link {
		color: $white !important;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

@media only screen and (max-width: 680px) {
	.project {
		font-size: 2.4rem;

		&__description {
			font-size: 2rem;

			&--body {
				font-size: inherit;
			}
		}
	}
}
