.footer {
	&__nav {
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: space-between;
		@media only screen and (max-width: 680px) {
			flex-direction: column-reverse;
			align-items: flex-start;
			padding: 0 3.2rem;
		}
	}

	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		list-style: none;
		@media only screen and (max-width: 680px) {
			flex-direction: column;
			align-items: flex-start;
			padding: 0 !important;
		}
	}

	&__item {
		&:not(:last-child) {
			margin-right: 6rem;
			@media only screen and (max-width: 680px) {
				margin: 0;
				margin-bottom: 3rem;
			}
		}
	}

	&__link {
		font-weight: 700;
	}
}
