a {
	color: $navy;
	text-decoration: none;
	outline: 0;

	&:focus {
		color: $fuchsia;
	}
}

.link,
.navigation,
.overlay,
.project,
.footer {
	&__link {
		&:link,
		&:visited {
			color: inherit;
			font-weight: 600;
			padding: 0.5rem 1rem;
			background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-primary 50%);
			background-size: 230%;
			transition: all 0.4s;
		}

		&:hover,
		&:active,
		&:focus {
			background-position: 100%;
			color: $white;
			_transform: translateX(-1rem);
		}
	}
}
