.navigation {
	_width: 80vw;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 10rem;

	&__brand {
		img {
			width: 100%;
		}
	}

	&__nav {
		_width: 50vw;
	}

	&__list {
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
	}

	&__item {
		list-style: none;

		&:not(:last-child) {
			margin-right: 1.2rem;
		}
	}

	&__button {
		background-color: $color-background;
		height: 6rem;
		width: 6rem;
		border-radius: 3px;
		box-shadow: 0 1rem 3rem rgba($color: #000000, $alpha: 0.2);
		text-align: center;
		cursor: pointer;
		position: relative;
		display: none;
	}

	&__icon {
		position: relative;
		margin-top: 3.5rem;
		&,
		&::after,
		&::before {
			width: 3rem;
			height: 2px;
			background-color: #000000;
			display: inline-block;
		}

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 0;
			transition: all 0.2s ease;
		}

		&::before {
			top: -0.8rem;
		}
		&::after {
			top: 0.8rem;
		}
	}

	&__button:hover &__icon {
		background-color: transparent;
	}

	&__button:hover &__icon::before {
		top: 0;
		transform: rotate(135deg);
	}

	&__button:hover &__icon::after {
		top: 0;
		transform: rotate(-135deg);
	}

	&-toggle {
		display: none;
	}

	&-toggle ~ &__nav {
		_display: none;
	}

	&-show {
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		width: 20vw;
		height: 100vh;
		background-color: $color-primary;
		z-index: 9999;
	}

	&-show &__list {
		flex-direction: column;
		align-items: flex-end;
		color: $white;
		padding: 4rem;
		margin-top: 6rem;
	}

	&-show &__item {
		display: block;
		font-size: 2.4rem;
		text-align: right;
		&:not(:last-child) {
			margin: 0;
			margin-bottom: 1.2rem;
		}
	}
}

@media only screen and (max-width: 680px) {
	.navigation {
		padding: 2rem;
		&__nav {
			_width: 50vw;
			display: none;
		}

		&__button {
			display: block;
		}
		&-toggle {
			display: block;
		}

		&-toggle ~ &__nav {
			display: none;
		}
	}
}
