//	Headings

@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,900");

.gigantic,
.huge,
.large,
.bigger,
.big,
h1,
h2,
h3,
h4,
h5,
h6 {
	color: #222;
	font-weight: bold;
	font-family: "Roboto", sans-serif;
	font-weight: 900;
}

.gigantic {
	font-size: 110px;
	line-height: 1.09;
	letter-spacing: -2px;
}

.huge,
h1 {
	font-size: 68px;
	line-height: 1.05;
	@media only screen and (max-width: 680px) {
		font-size: 3.2rem;
	}
}

.large,
h2 {
	font-size: 42px;
	line-height: 1.14;
	@media only screen and (max-width: 680px) {
		font-size: 2.4rem;
	}
}

.bigger,
h3 {
	font-size: 26px;
	line-height: 1.38;
	@media only screen and (max-width: 680px) {
		font-size: 1.8rem;
	}
}

.big,
h4 {
	font-size: 22px;
	line-height: 1.38;
	@media only screen and (max-width: 680px) {
		font-size: 1.4rem;
	}
}

.small,
small {
	font-size: 1rem;
	line-height: 1.2;
}

// Basic Text Style

p {
	margin: 0 0 20px 0;
	font-weight: 400;
	font-size: 1.4rem;
}

em {
	font-style: italic;
}
strong {
	font-weight: bold;
}

hr {
	border: solid #ddd;
	border-width: 1px 0 0;
	clear: both;
	margin: 10px 0 30px;
	height: 0;
}

.heading {
	&-primary {
		font-size: 3rem;
		font-weight: 400;
		@media only screen and (max-width: 680px) {
			font-size: 3.2rem;
			font-weight: 500;
		}
	}

	&-secondary {
		font-size: 2rem;
		font-weight: 400;
		border-bottom: 0.2rem solid $color-primary;
		display: inline-block;
		@media only screen and (max-width: 680px) {
			font-size: 3rem;
			font-weight: 500;
		}
	}
}

.body {
	&-primary {
		font-size: 1.8rem;
		@media only screen and (max-width: 680px) {
			font-size: 2.4rem;
		}
	}

	&-secondary {
		font-size: 1.6rem;
		@media only screen and (max-width: 680px) {
			font-size: 2rem;
		}
	}
}

.hero {
	&__title {
		font-weight: 500;
		font-size: 6rem;
		padding: 2rem 1;
		color: inherit;
		&--sub {
			display: block;
			font-size: 2rem;
			text-transform: uppercase;
			letter-spacing: 2px;
			font-weight: 400;
			color: $color-secondary;
		}
	}

	&__text {
		font-size: 3rem;
		line-height: 1.2;
		margin: 0;
		margin-left: 3rem;
	}
}

@media only screen and (max-width: 680px) {
	.hero {
		&__title {
			font-size: 8rem;

			&--sub {
				font-size: 3rem;
			}
		}

		&__text {
			font-size: 3.6rem;
			margin-left: 0;
		}
	}
}
