.hero-section {
	height: 70vh;
	display: flex;
	align-items: center;
	justify-content: space-between;

	&__left {
		_padding: 3rem 0;
		width: 40%;
	}

	&__right {
		_padding: 3rem 0;
		width: 50%;
		text-align: left;
	}
}

.section-about {
	.about {
		&__img {
			width: 100%;
			max-width: 100%;
			border-radius: 1.6rem;
			filter: grayscale(0) brightness(0.5) contrast(1.1);

			@media only screen and (max-width: 680px) {
				width: 70%;
				margin: 0 auto;
				_display: none;
			}
		}

		.col-left {
			@media only screen and (max-width: 680px) {
				width: 90%;
			}
		}
		.col-right {
			@media only screen and (max-width: 680px) {
				width: 90%;
			}
		}
	}
	.row {
		@media only screen and (max-width: 680px) {
			flex-direction: column;
		}
	}

	@media only screen and (max-width: 680px) {
		margin-bottom: 0rem !important;
	}
}

.section-playground {
	.row {
		@media only screen and (max-width: 680px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	@media only screen and (max-width: 680px) {
		margin-top: 0 !important;
		padding: 0 3.2rem;
	}
}

.scroll {
	&-down {
		display: inline-block;
		text-align: center;
		&__text {
			font-size: 1.6rem;
		}
		&__icon {
			width: 24px;
			animation: bounce 1s 3s infinite;

			img {
				width: 100%;
			}
		}
	}

	&-top {
		display: none;
		width: 4rem;
		_height: 4rem;
		text-align: center;
		position: fixed;
		bottom: 2.5rem;
		right: 2.5rem;
		border: none;
		padding: 0.8rem;
		border-radius: 3px;
		transform: rotate(180deg);
		z-index: 1000;
		transition: all 0.2s ease;
		img {
			width: 100%;
		}

		&:hover {
			transform: rotate(180deg) scale(1.1);
		}
	}
}

.work {
	&__list {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
	}

	&__item {
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
		@media only screen and (max-width: 680px) {
			font-size: 2.4rem;
		}
	}

	&__link {
		&:link,
		&:visited {
			font-size: 1.6rem;
			font-weight: bold;

			@media only screen and (max-width: 680px) {
				font-size: 2.4rem;
			}
		}
	}
}

.show {
	display: block;
}

@media only screen and (max-width: 680px) {
	.hero-section {
		height: 50vh;
		flex-direction: column;
		justify-content: center;
		&__left {
			_padding: 3rem 0;
			width: 90%;
		}

		&__right {
			_padding: 3rem 0;
			width: 90%;
			text-align: left;
			margin-top: 2rem;
		}
	}

	.scroll {
		&-down {
			display: none;
		}
	}
}

.section-contact {
	.row {
		@media only screen and (max-width: 680px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}
	.arrow-right {
		@media only screen and (max-width: 680px) {
			width: 2.4rem;
		}
	}

	.col-left {
		@media only screen and (max-width: 680px) {
			width: 90%;
		}
	}
	.col-right {
		@media only screen and (max-width: 680px) {
			margin-top: 4rem;
			width: 90%;
		}
	}

	@media only screen and (max-width: 680px) {
		margin-top: 0 !important;
		padding: 0 3.2rem;
	}
}
