.wrapper {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
}

.row {
	display: flex;
	align-items: center;
	flex-direction: row;

	@media only screen and (max-width: 680px) {
		flex-direction: column-reverse;
	}
}

.col-left {
	width: 40%;

	@media only screen and (max-width: 680px) {
		width: 90%;
	}
}

.col-right {
	width: 50%;
	@media only screen and (max-width: 680px) {
		width: 80%;
		margin-bottom: 4rem;
	}
}
